/*
= bread crumb
*/
.c-breadcrumb {
	.breadcrumb-list {
		&:after {
			@include after;
		}
		> li {
			position: relative;
			margin-right: $gap-8;
			float: left;
			font: {
				size: $fs-small;
			}
			color: $color-gray-3;

			& ~ li {
				&:before {
					float: left;
					padding-right: $gap-8;
					color: $color-gray-3;
					content: '/';
				}
			}

			&.on {
				color: $color-navy-1;
			}
		}
	}
}

.top_head {
	.c-breadcrumb {
		& + .c-title-field {
			:after {
				@include after;
			}
			position: relative;
			margin-top: $gap-12;
			padding-bottom: $gap-12;
			.buttons {
				position: absolute;
				top: -6px;
				right: 0;
			}
		}
	}
}
.co-table.cont-type04.tbl-chkup-result01 {
	border-right: 1px solid $table-border-color;
}

// = c-titcont-list
.c-titcont-list {
	display: flex;
	align-items: center;
	> dl {
		display: table;
		& ~ dl {
			margin-left: $gap-20;
		}
		dt,
		dd {
			font: {
				size: $fs-small;
				weight: $weight-300;
			}
			display: table-cell;
			font-size: $fs-small;
		}
		dt {
			color: $color-gray-2;
		}
		dd {
			padding-left: $gap-12;
			color: $color-gray-3;
		}
	}
}
.c-txtarea-field {
	& + .c-titcont-list {
		margin-top: $gap-12;
	}
}

// =Memo
.c-memo-card {
	> a {
		border: 1px solid $color-gray-5;
		background-color: #fff;
		border-radius: $radius-12;
		padding: 12px 20px;
		margin-top: 12px;
		margin-bottom: 12px;
		display: flex;
		flex-wrap: nowrap;
		width: 100%;
		align-items: flex-start;

		> .col-cont.on {
			& + .col-name {
				> i {
					transform: rotate(-180deg);
				}
			}
			.col-name {
				i {
					transform: rotate(-180deg);
				}
			}
		}
	}
	.col-date,
	.col-name {
		flex-shrink: 0;
		flex-grow: 0;
	}
	.col-date {
		width: 110px;
	}
	.col-cont {
		padding-right: 40px;
		flex-grow: 1;
		overflow: hidden;
		height: 24px;

		&.on {
			.detail-cont {
				&.on {
					&:after {
						display: none;
					}
				}
			}
		}

		.detail-cont {
			min-height: 24px;
			line-height: 24px;
			position: relative;
			word-break: break-all;
			&.on {
				&:after {
					content: '...';
					position: absolute;
					top: 0;
					right: 0;
					display: inline-block;
					padding-left: 1px;
					background-color: $color-white-1;
				}
			}
		}
	}
	.col-name {
		width: 100px;
		&:after {
			@include after;
		}
		> .name {
			display: inline-block;
		}
		i {
			float: right;
			//  margin-left: $gap-40;
		}
	}

	.arrow {
		background: url($image-ico-path + 'ico_arrow_07.svg') 0/20px no-repeat;
		width: 20px;
		height: 20px;
		content: '';
		display: inline-block;
		overflow: hidden;
		vertical-align: top;
	}
}

// = c-card-panel

/*
	= Card Type
*/
.c-title-field {
	& + .c-card-article {
		margin-top: $gap-2;
	}
}

.c-card-article {
	& + .c-card-article {
		margin-top: $gap-10;
	}
}

.c-card-article {
	.c-card {
		display: flex;
		flex-direction: column;
		min-width: 0;
		word-wrap: break-word;
		background-color: $color-white-1;
		border-radius: $radius-8;
		border: 1px solid $color-gray-5;
	}
}

.c-card-article {
	> [class*='col'],
	> [class*='col-'] {
		padding: {
			left: $gap-12;
			right: $gap-12;
		}
	}
	&.type01 {
		[class='col-3'] {
			padding: {
				top: $gap-12;
				bottom: $gap-12;
				left: $gap-12;
				right: $gap-12;
			}
		}
	}
}

.c-card {
	//+ type01 (추가 대상자)
	&.type01 {
		$card-type01-padding: 0.428rem; // 6px
		position: relative;

		.card-body {
			padding: 0.428rem; // 6px
		}

		.add-entry {
			display: flex;
			@include justify-row;
			align-items: center;
			padding-right: 0.928rem;
		}

		.info > ul {
			&:after {
				@include after;
			}
			> li {
				padding: $card-type01-padding;
				float: left;
			}
		}

		.c-btn-del {
			background: url($image-ico-path + 'ico_del_01.svg') center/100% no-repeat;
			width: 24px;
			height: 24px;
		}

		& + .add-entry {
			margin-top: 12px;
			align-items: center;
		}
	}

	// + type02 {
	&.type02 {
		padding: 28px 20px;
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		.txt {
			line-height: 1.7;
		}

		.cnt {
			line-height: 1.9;
			color: $color-blue-1;
			font: {
				weight: 400;
				size: $p-fs-large-2;
			}
		}
	}

	&.type03 {
		display: flex;
		flex-direction: column;
		padding: 12px 20px;

		.name {
			font-size: $fs-small;
		}
		.c-multi-input-group-02 {
			display: flex;
			align-items: center;
			margin-top: 8px;
			display: flex;
			table-layout: fixed;
			overflow: hidden;
			width: 100%;
			.input-col {
				&.time {
					margin-left: 8px;
					> input[type='text'] {
						text-align: center;
						padding: 2px;
						width: 40px;
						height: 29px;
					}
				}
			}
		}
	}
}

//  + c-plan-chk-group
.c-plan-chk-group {
	display: flex;
	align-items: center;
	.c-multi-input-group-02 {
		display: inline-block;
	}
}

.c-top-cont {
	margin-bottom: 12px;
}
// 예약일정 관리 테이블
.c-plan-chk-group {
	&.type02 {
		.c-multi-input-group-02 {
		}
	}
}

// = reservation-detail
.c-layout-type01 {
	overflow: hidden;
	.left-wrap,
	.right-wrap {
		height: calc(100vh - 330px);
		overflow: hidden;
		> .c-inner {
			height: 100%;
			overflow-y: auto;
			padding-right: 10px;

			@include scrollType01;
		}
	}
}

// [Calendar]
// = c-tbl-calendar
.c-calendar-container {
	padding: 24px 20px;
	background-color: $color-white-1;
	border-radius: $radius-8;
	border: 1px solid $color-gray-5;
}
.c-calendar-wrap {
	.top-field {
		position: relative;
		border-bottom: 2px solid $color-gray-5;
		padding-bottom: 20px;
		&:after {
			@include after;
		}

		.c-controls {
			display: flex;
			justify-content: center;
			align-items: center;

			.year-month {
				padding: 0 20px;
				> p {
					font: {
						size: 28px;
						weight: $weight-400;
					}
					line-height: 41px;
				}
			}
			> button {
				width: 20px;
				height: 21px;
				border-radius: $radius-4;
				background: url($image-ico-path + 'ico_arrow_08.svg') $color-navy-1
					center/6px auto no-repeat;
				transform: rotate(-180deg);

				&.btn-next {
					transform: rotate(0deg);
				}
			}
		}

		.c-badge-info {
			position: absolute;
			top: 7px;
			right: 0;
			display: flex;
			> li {
				display: flex;
				align-items: center;
				i {
					display: inline-flex;
					width: 18px;
					height: 18px;
					border: 1px solid $color-gray-4;
					border-radius: $radius-4;
				}
				span {
					display: inline-block;
					margin-left: 8px;
					color: $color-gray-2;
					line-height: 24px;
				}
				& ~ li {
					margin-left: 24px;
				}
			}
		}
	}

	background-color: $color-white-1;

	.weekdays {
		display: flex;

		> li {
			text-align: center;
			padding: 29px 0;
			border-bottom: 2px solid $color-gray-5;
			background-color: $color-white-1;
			line-height: 23px;
			font: {
				size: 16px;
				weight: $weight-400;
			}
		}
	}
	.weekdays,
	.calendar_days {
		> li {
			width: calc(100% / 7);
			display: inline-block;
		}
	}

	// + calendar_days
	.calendar_days {
		display: flex;
		flex-wrap: wrap;
		> li {
			padding: 12px;
			height: 157px;
			border-radius: $radius-8;
			border: 1px solid $color-gray-5;
			background-color: #fff;

			&.is-holiday,
			&.is-reserve-unable,
			&.is-reserve-closed {
				background-color: $color-gray-5;
				opacity: 0.5;
				border-color: $color-white-1;
			}

			&.is-reserve-unable {
				.day {
					color: $color-black-op4;
				}
			}
			&.is-reserve-closed {
				.day {
					color: $color-black-op8;
				}
			}
		}
	}

	.day-top {
		display: flex;
		align-items: center;
		.day {
			font: {
				size: $p-fs-large-2;
				weight: $weight-400;
			}
			line-height: 29px;
		}
		.state {
			margin-left: 10px;
			> span {
				border-radius: $radius-8;
				background-color: $color-orange-1;
				text-align: center;
				display: inline-block;
				padding: 2px 16px;
				font-size: $fs-small;
				line-height: 20px;
				color: $color-white-1;
			}

			.holiday {
				background-color: $color-orange-1;
			}
			.reserve-available {
				background-color: $color-blue-1;
			}
			.reserve-unable {
				background-color: $color-gray-3;
			}
			.reserve-closed {
				background-color: $color-navy-1;
			}
		}
	}

	.day-cont {
		margin-top: 8px;
	}

	.reserve-list {
		> li {
			> a {
				color: $color-blue-1;
				text-decoration: underline;
				font-size: $fs-nomal;
				line-height: 24px;
			}
		}
	}
	.txt-sun {
		color: $color-orange-1;
	}
}

.c-article {
	&.mod-reservation {
		padding: $gap-12;
		height: 384px;
		width: 100%;
		display: flex;
		.c-inner-left,
		.c-inner-right {
			height: 100%;
			overflow-x: hidden;
			overflow-y: auto;
			@include scrollType01;
		}
		.c-inner-left {
			width: 180px;
		}

		.c-inner-right {
			padding-left: 24px;
			flex-grow: 1;
		}

		.rsv-mod-list01 {
			> li {
				padding-right: 12px;
				button {
					text-align: left;
					padding: 10px 20px;
					display: block;
					min-width: 160px;
					display: block;
					border-radius: $radius-6;
					background-color: $color-white-1;
					span {
						color: $color-black-1;
						font {
							size: $fs-nomal;
						}
						line-height: $g-lineheight-2;
					}

					&.on {
						background-color: $color-navy-1;
						span {
							color: $color-white-1;
						}
					}
				}
			}
		}

		.rsv-mode-list02 {
			> li {
				padding-right: 12px;
				& + li {
					margin-top: 8px;
				}
			}
		}

		.rsv-mod-detail {
			padding: 12px;
			border: 1px solid $color-gray-5;
			border-radius: $radius-8;
			display: flex;
			width: 100%;
			align-items: center;

			color: $color-gray-2;

			.c-chkbox {
				flex-grow: 1;
			}
			.region {
				padding: 2px 16px;
			}

			.link {
				margin-left: 12px;
				padding: 2px 16px;
			}
		}
	}
}

//  ======================================== subCont

// [ETC]
#container {
	.etc-wrapper {
		min-width: 1200px;
		padding-left: 0;
		background-color: $color-gray-12;
	}
}
// + c-full-info-wrap
.c-full-info-wrap {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	padding: {
		left: 14.285rem;
		right: 14.285rem;
	}
	height: 100%;

	.c-full-info-field {
		justify-content: space-between;
		align-items: center;
		display: flex;
		width: 100%;

		// + is-error
		&.is-error {
			.info-cont {
				line-height: 36px;
			}

			.img {
				img {
					width: 271px;
					height: 282px;
				}
			}
		}

		// + is-update
		&.is-update {
			.img {
				img {
					width: 328px;
					height: 282px;
				}
			}
		}
	}

	.c-cont-field {
		color: rgba(28, 28, 27, 0.8);

		.title {
			@include fontStyle(80px, 116px, 700);
		}
		.info-cont {
			margin-top: 40px;
			@include fontStyle(24px, 36px, 700);
		}
		.sub-cont {
			margin-top: 8px;
		}

		.co-btn {
			margin-top: 40px;
			display: inline-block;
			border-radius: $radius-8;
			min-width: 134px;
			padding: 8px 40px;
			height: 46px;

			span {
				color: $color-white-1;
				line-height: 30px;
			}
		}
	}

	.footer {
		position: absolute;
		bottom: 40px;
		left: 0;
		right: 0;
		text-align: center;
		margin: 0 auto;
	}
}

// = spinner
.spinner-field {
	position: absolute;
	top: 0%;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	display: block;

	> div {
		@include position(50%, 50%);
	}
}
.spinner {
	border: 6px solid #e7e9ea;
	border-radius: 50%;
	//  border-right: 10px solid $color-blue-1;
	width: 58px;
	height: 58px;
	animation: spinner 1.5s linear infinite;
	position: relative;
	&:after {
		content: '';
		background: url($image-path + 'spinner.svg') 0 0/28px auto no-repeat;
		display: inline-block;
		width: 28px;
		height: 28px;
		position: absolute;
		top: -6px;
		left: 23px;
	}
}

@keyframes spinner {
	0% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(360deg);
	}
}

// ================================================= STYLE

/*
 = Login
 */
#container {
	.login-wrapper {
		padding-left: 0;
		min-height: 800px;
		height: inherit;
	}
}
.login-wrapper {
	.login-wrap {
		display: flex;
		width: 100%;
		height: 100%;
		flex: 1 0 auto;
	}
	.side-area {
		// width: 560px;
		// min-width: 560px;
		// height: 100%;
		// display: flex;
		// align-items: center;
		// border-right: 1px solid $color-gray-5;
		// background-color: $color-white-1;
		// padding: 197px 90px;
		// position: relative;

		h1.title {
			display: block;
			width: 100%;
			text-align: left;
			font: {
				size: $p-fs-large-1;
				weight: $weight-500;
			}
			line-height: 58px;
		}
	}
	.contents-area {
		width: 100%;
		display: flex;
		flex: 1 1 100%;
		flex-direction: column;
	}
	.login-img {
		min-height: 781px;
	}
	.login-box {
		margin-top: 20px;

		.c-input-btn-group {
			flex-wrap: wrap;
			$gap: $gap-8;
			display: flex;
			flex: 1 1 auto;
			margin: {
				left: -$gap;
				right: -$gap;
			}

			[class|='col'] {
				padding: {
					left: $gap;
					right: $gap;
				}
			}
		}

		.login-input {
			input[type='text'],
			input[type='password'],
			.c-input-btn-group {
				width: 100%;
				flex: 0 0 0;

				& + input[type='text'],
				& + input[type='password'] {
					margin-top: 16px;
				}
			}

			.row {
				width: 100%;
			}

			.search-btn-group {
				position: relative;
				&.row {
					width: auto;
				}
				$gap: 8px;
				margin: {
					left: 0;
					right: 0;
				}

				.col {
					padding: {
						left: $gap;
						right: $gap;
					}
				}
				.input-button {
