			.search-related-field {
				position: absolute;
				top: 48px;
				left: 0;
				right: 0;
				z-index: 10;
				.search-list {
					background-color: $color-white-1;
					border-radius: 8px;
					border: 1px solid $color-gray-5;
					overflow: hidden;
					box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
					min-width: 100%;
					padding: {
						top: 4px;
						bottom: 4px;
					}

					> li {
						& ~ li {
							border-top: 1px solid $color-gray-5;
						}
						a {
							padding: 7px 20px;
							display: block;
							color: $color-gray-1;
							@include coBodyTxt;
							&:hover {
								color: $color-navy-1;
							}
						}
					}
				}
			}
		}

		.info-cont {
			color: $color-gray-2;
			margin-top: 12px;
		}

		.info-chk {
			margin-top: 40px;
			&:after {
				@include after;
			}

			.c-chkbox {
				float: left;
				.chk-label {
					line-height: $p-lineheight-normal;
				}
				& + .link-guide {
					margin-top: 5px;
				}
			}

			.link-guide {
				float: right;
			}
		}

		.c-btn {
			&.btn-login {
				margin-top: 40px;
				border-radius: $radius-4;
				width: 100%;
				padding: {
					top: 16px;
					bottom: 16px;
				}
				color: $color-white-1;
				line-height: 20px;
			}
		}
	}

	.link-guide {
		font: {
			size: $fs-small;
		}
		color: $color-blue-1;
		line-height: 1.6;
		display: inline-block;
		text-decoration: underline;
	}

	.login-btn {
		width: 100%;
		color: $color-white-1;
		background-color: $color-navy-1;
		margin-top: $gap-12;
		font-size: 0;
		height: auto;
		padding: {
			top: 16px;
			bottom: 16px;
		}
		> span {
			@include fontStyle(14px, 20px, 300);
		}
	}

	.login-info-list {
		margin: 20px auto 0;
		display: table;
		&:after {
			@include after;
		}

		> li {
			color: $color-gray-3;
			@include coTxtCaption;
			float: left;
			position: relative;
			& ~ li {
				padding-left: 16px;
				&:before {
					position: absolute;
					top: 6px;
					left: 8px;
					content: '';
					width: 1px;
					height: 15px;
					display: inline-block;
					background-color: $color-gray-4;
				}
			}
			> a,
			> button {
				display: inline-block;
			}
		}
	}

	.use-info {
		margin-top: 100px;
