		//   top: 0;
		//   opacity: 0;
		//   content: '';
		//   transition: opacity 0.5s;
		// }

		&:hover {
			&:before {
				opacity: 1;
			}
		}

		&.prev {
			left: 0;
			&:hover {
				&:before {
					background: linear-gradient(
						90deg,
						rgba(25, 25, 25, 0.1) 0%,
						rgba(217, 217, 217, 0) 38.05%
					);
				}
			}
		}
		&.next {
			right: 0;
