	.main-slide-btn-next {
		right: 0;
		.ico {
			right: 20px;
			background-position: 0 -38px;
		}
		&:hover {
			&:before {
				position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				content: '';
				background: linear-gradient(
					90deg,
					rgba(25, 25, 25, 0.1) 0%,
					rgba(217, 217, 217, 0) 38.05%
