		> span {
			@include coTxtSmall01;
			color: $color-gray-2;
			position: relative;
			padding-left: 28px;
			&:before {
				content: '';
				top: 50%;
				left: 0;
				transform: translateY(-50%);
				position: absolute;
				background: url(../images/ico/ico_sprites_01.svg) -912px 0 / auto 218px no-repeat;
				width: 28px;
				height: 28px;
				display: inline-block;
				overflow: hidden;
				text-indent: 100%;
				white-space: nowrap;
				display: inline-block;
			}
		}
	}
	// + type02
	.btn-bar-toggle {
		position: absolute;
		right: 90px;
		top: -36px;
		border: 1px solid $color-gray-4;
		border-radius: 12px 12px 0px 0px;
		background-color: $color-white-1;
		display: inline-block;
		width: 60px;
		height: 36px;

		> span {
			background: url($image-ico-sprite01) -76px 0 / $image-ico-sprite01-size no-repeat;
			width: 28px;
			height: 28px;
			transform: rotate(-180deg);
		}

		&.is-open {
			> span {
				transform: rotate(0);
			}
			& ~ .btm-compare-list-field {
				display: block;
			}
		}
	}
	.btn-reservation-prev,
	.btn-reservation-next {
		span {
			&:before {
				position: absolute;
				top: -4px;
				content: '';
				background: {
					image: url($image-ico-path + 'ico_btm_bar_sprite01.svg');
					size: auto 28px;
					repeat: no-repeat;
				}
				width: 28px;
				height: 28px;
				display: inline-block;
			}
		}

		&:hover {
			opacity: 0.4;
		}
	}

	.btn-reservation-prev {
		border: 1px solid $color-navy-1;
		background-color: $color-white-1;
		border-color: $color-navy-1-op2;
		color: $color-navy-1-op2;
		padding: 8px 20px 8px 8px;
		cursor: default;
		span {
			padding-left: 28px;
			&:before {
				background-position: 0 0;
				left: 0;
			}
		}

		&:hover {
			opacity: 1;
		}

		&.is-active {
			cursor: pointer;
			border-color: $color-navy-1;
			color: $color-navy-1;
			span {
				&:before {
					background-position: -38px 0;
				}
			}
			&:hover {
				span {
					&:before {
						animation: rsvPrevBtnAni 0.9s 3 linear;
					}
				}
			}
		}
	}
	.btn-reservation-next {
		padding: 8px 8px 8px 20px;
		span {
			padding-right: 28px;
			&:before {
				background-position: -76px 0;
				right: 0;
			}
		}
		&:hover {
			opacity: 1;
			span {
				&:before {
					animation: rsvNextBtnAni 0.9s 3 linear;
				}
			}
		}
	}
}

@keyframes rsvNextBtnAni {
	from {
		right: 0;
	}
	50% {
		right: -3px;
	}
	to {
		right: 0;
	}
}

@keyframes rsvPrevBtnAni {
	from {
		left: 0;
