.mychkup-history-field {
	margin-top: 12px;
	overflow: hidden;
	border-radius: 12px;
	position: relative;

	padding: {
		left: 48px;
		right: 48px;
	}

	.mychkup-history-swiper {
		overflow: hidden;
		width: auto;
	}
	.swiper {
		width: 100%;
		height: 100%;
	}

	.swiper-slide {
		width: 296px;
		display: flex;
		align-items: center;
		padding: 1px;
		& + .swiper-slide {
			margin-left: 20px;
		}
	}

	.slide-card {
		width: 100%;
		position: relative;
		border-radius: $radius-12;
		overflow: hidden;
		min-height: 234px;
	}
	.co-slide-btn-prev,
	.co-slide-btn-next {
		cursor: default;
		display: block;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
	}

	.co-slide-btn-prev {
		left: 0;
		&:hover {
			background-position: 0 0;
		}
	}
	.co-slide-btn-next {
		right: 0;
		background-position: 0 -38px;
	}

	.mychkup-history-swiper {
		overflow: hidden;
	}

	&:before {
		content: none;
	}
	//  is-active
	&.is-active {
		&:before {
			position: absolute;
			top: 1px;
			bottom: 1px;
			right: 48px;
			content: '';
			background: linear-gradient(
				90deg,
				rgba(25, 25, 25, 0.025) 0%,
				rgba(217, 217, 217, 0) 38.05%
			);
			transform: rotate(-180deg);
			width: 96px;
			// border-radius: 14px 0 0 14px;
		}
		.co-slide-btn-prev,
		.co-slide-btn-next {
			cursor: pointer;
		}
		.co-slide-btn-prev {
			&:hover {
				background-position: 0 -76px;
			}
		}
		.co-slide-btn-next {
			&:hover {
				background-position: 0 -114px;
			}
		}
	}

	.co-slide-btn-prev {
		&.swiper-button-disabled {
			background-position-y: -152px;
			opacity: 1 !important;
			&:hover {
				background-position-y: -152px;
			}
		}
	}
	.co-slide-btn-next {
		&.swiper-button-disabled {
			background-position-y: -190px;
			opacity: 1 !important;
			&:hover {
				background-position-y: -190px;
			}
		}
	}
}

