// = [JO]
.jo-progress-bar {
	border-radius: 8px;
	position: relative;
	overflow: hidden;
	width: 100%;
	height: 32px;
	background-color: $color-gray-8;

	.mark {
		position: absolute;
		left: 0;
		right: 0;
		width: 100%;
		display: flex;
	}
	.jo-progress {
		border-radius: 8px;
		display: block;
		width: 0%;
		height: 100%;
		background: #253981;
		background-image: linear-gradient(
			-45deg,
			rgba(255, 255, 255, 0.125) 25%,
			transparent 25%,
			transparent 50%,
			rgba(255, 255, 255, 0.125) 50%,
			rgba(255, 255, 255, 0.125) 75%,
			transparent 75%,
			transparent
		);
		background-size: 18px 18px;
		animation: aniProgressBar 3s linear infinite;
	}
}

.jo-progress-field {
	padding: 24px 20px;
	background-color: $color-white-3;
	border-radius: 12px;
	border: 1px solid $color-gray-4;

	.result-value {
		@include coSubTitle;
		& + .progress-box {
			margin-top: 12px;
		}
	}

	.progress-box {
		position: relative;
		.mark {
			&.step01,
			&.step02 {
				height: 24px;
				margin-top: 4px;
				width: 100%;
			}
			> div {
				position: absolute;
				left: 0;
				width: 100%;
				> span {
					position: absolute;
					&:last-of-type {
						right: 0;
					}
				}
			}
			&.step01 {
				span {
					&:first-child {
						left: 0;
					}
					&:nth-child(2) {
						left: 25%;
					}
					&:nth-child(3) {
						left: 50%;
					}
					&:nth-child(4) {
						left: 75%;
					}
				}
			}

			&.step02 {
				span {
					margin-left: -30px;
					&:first-child {
						left: 25%;
					}
					&:nth-child(2) {
						left: 50%;
					}
					&:nth-child(3) {
						left: 70%;
					}
				}
			}
		}
	}
}

@keyframes aniProgressBar {
	0% {
		background-position: 0 0;
	}

	100% {
		background-position: 16px 16px;
	}
}

